.home-info-2 {
  h1, p {
    color: #000;
  }
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.home-about-us-3 {
  color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  h1, p {
    text-align: center;
  }
  ul {
    font-size: 26px;
  }
  background: url("http://78.46.184.51:4000/images/image1.jpg") no-repeat center;
  background-size: cover;
}

.qari-tesakneri-masin-4 {
  padding: 30px 0;
  box-sizing: border-box;
  text-align: center;
  h1 {
    color: black;
  }
}

.arac-gorcer-slider-5 {
  color: #fff;
  h1 {
    text-align: center;
  }
  ul {
    margin: auto;
    li {
      margin-bottom: 15px;
    }
  }
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  width: 100%;
  height: 500px;
  background: url("http://78.46.184.51:4000/images/image2.jpg") no-repeat center;
  background-size: cover;
}

.contact-us-6 {
  text-align: center;
  .contact-us-container{
    display: flex;
    justify-content: space-between;
  }
  .google-map {
    max-width: 450px;
    img {
      width: 100%;
    }
  }
  button {
    color: black;
  }
  form {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
  }
  @media only screen and (max-width: 768px) {
    .contact-us-container {
      flex-direction: column;
      align-items: center;
    }
  }
  @media only screen and (max-width: 320px) {
    font-size: 14px;
    h1 {
      font-size: 1.5em;
    }
  }
}

